import React, { useState } from 'react';
import { AppBar, Toolbar, Typography, IconButton, Badge } from '@mui/material';
import MenuIcon from '@mui/icons-material/MoreVert';
import MenuBar from '../../Menu/MenuBar';
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos'; //    
import {useNavigate,useParams } from 'react-router-dom';
import { useSelector } from 'react-redux';

const Header = () => {
  const [drawerOpen, setDrawerOpen] = useState(false); //   drawerOpen  setDrawerOpen
  const {accountId} = useParams();
  const list = useSelector((state) => state.work.work);
  const goWork = () => navigate(`/${accountId}/work`);
  const navigate = useNavigate();
  const goBack = () => navigate(-1);
    
  const toggleDrawer = () => {
    setDrawerOpen(!drawerOpen);
  };

  return (
    <div>
      <AppBar position="static" sx={{ backgroundColor: 'white' }}>
        <Toolbar>
        <IconButton edge="start" aria-label="back" sx={{ color: '#005475' }} onClick={() => goBack()}> {/*  edge  start     */}
            <ArrowBackIosIcon />
          </IconButton>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1, color: '#005475', fontSize: '18px', fontFamily: "'Montserrat', sans-serif" , fontWeight: '500'  }}>
            
          </Typography>
          <IconButton onClick={() => goWork()}>
          <Badge badgeContent={list.length ? list.length : "0" } sx={{
              "& .MuiBadge-badge": {
                backgroundColor:"#005475", //      #005475    
                color:"white",//     
                border: "2px solid white",//   
                width: '26px', //  
                height: '26px', //  
                borderRadius: '50%', //  
                fontSize: '15px',
              },
            }}></Badge>

          </IconButton>
          <IconButton edge="end" aria-label="menu" sx={{ color: '#005475' }} onClick={toggleDrawer}>
            <MenuIcon />
          </IconButton>
        </Toolbar>
      </AppBar>
      <MenuBar toggleDrawer={toggleDrawer} drawerOpen={drawerOpen} /> {/*  toggleDrawer  drawerOpen  MenuBar */}
    </div>
  );
};

export default Header;
