//   NotificationPrompt.js

import React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import { useParams } from "react-router-dom";
import { main } from './script.js';

function NotificationPrompt({ onClose }) { //   onClose  
  const { accountId } = useParams();
  const handleClose = () => {
    onClose(); //  onClose    
  };

  const allowedPush = async () => {
    await main(accountId);
    handleClose();
  };

  return (
    <Dialog open={onClose? true : false} onClose={handleClose}> {/*    */}
      <DialogTitle>{""}</DialogTitle>
      <DialogContent>
        <p>    ?</p>
      </DialogContent>
      <DialogActions sx={{justifyContent: "center"}}>
        <Button onClick={handleClose} variant="text" sx={{mr:'50px'}}></Button>
        <Button onClick={allowedPush} variant="contained"  sx={{ml:'50px'}}></Button>
      </DialogActions>
    </Dialog>
  );
}

export default NotificationPrompt;
