import { createSlice } from '@reduxjs/toolkit';

const postSlice = createSlice({
    name: 'post',
    initialState: {
      generation: '', //    
      booklet: '', //    
      accessType: '',
      quantity: '',
      city: '',
      productIds: [] //      
    },
    reducers: {
      setGeneration: (state, action) => {
        state.generation = action.payload;
      },
      setBuklet: (state, action) => {
        state.booklet = action.payload;
      },
      setAccessType: (state,action) => {
        state.accessType = action.payload;
      },
      setquantity: (state, action) => { 
        state.quantity = action.payload;
      },
      setCity: (state, action) => {
        state.city = action.payload;
      },
      setProductIds: (state, action) => { //    productIds
        state.productIds = action.payload;
      },
    },
});

export const { setGeneration, setBuklet, setAccessType, setquantity, setCity, setProductIds } = postSlice.actions; //  setProductIds  

export default postSlice.reducer;
