const checkPermission = () => {
    if (!('serviceWorker' in navigator)) {
        throw new Error("No support for service worker!");
    }

    if (!('Notification' in window)) {
        throw new Error("No support for notification API");
    }

    if (!('PushManager' in window)) {
        throw new Error("No support for Push API");
    }
};

const registerSW = async (accountId) => {
    console.log(`in registerSW ${accountId}`)
    try {
        const registration = await navigator.serviceWorker.register('https://24academy.ru/PWA/sw.js');
        console.log('Service Worker registered with scope:', registration.scope);

        //   Service Worker
        const registrationReady = await navigator.serviceWorker.ready;
        console.log('Service Worker is ready.');

        //     
        if (registrationReady.active) {
            registrationReady.active.postMessage({
                type: 'SET_ACCOUNT_ID',
                data: { accountId },
            });
        } else {
            console.warn('Service Worker is not yet active.');
        }

        return registrationReady;
    } catch (err) {
        console.error('Service Worker registration failed:', err);
    }
};

const requestNotificationPermission = async () => {
    const permission = await Notification.requestPermission();
    if (permission!== 'granted') {
        throw new Error("Notification permission not granted");
    }
};

const main = async (accountId) => {
    checkPermission();
    await requestNotificationPermission();
    console.log(`in Main ${accountId}`);
    await registerSW(accountId); //  accountId  registerSW
};

export { main };


